import React from "react"
import { PageLayout } from "../components/"
import Container from "react-bootstrap/Container"
import Image from "react-bootstrap/Image"
import { Link } from "gatsby"

export default function about() {
  return (
    <div id="fullPage">
      <PageLayout>
        <h1 className="pt-5 text-center">About Me</h1>
        <Container className="text-center">
          <Image
            roundedCircle
            width="340"
            height="340"
            src={`../../mydata/profilePicture.jpg`}
            alt="Sourav Kulkarni"
          />
          <article className="w-75 m-auto pt-2 text-justify">
            {/* <p className="text-center">
              {designations.map((attr, i) => (
                <span key={attr}>
                  &nbsp;<b>{attr}</b>&nbsp;
                  {i < designations.length - 1 && <>||</>}
                </span>
              ))}
            </p> */}
            <p className="i-5 mt-4 pt-2">
              Hello there! My name is <b>Sourav Kulkarni</b>. I am a computer
              science student current waiting for my MSc Media Informatics
              course to start in RWTH Aachen.
            </p>
            <p className="i-5">
              In my spare time you will most likely find me on local trails or
              wildlife hotspots with my camera. Occasionally, I read books or
              play some musical instruments....
            </p>
            <p className="i-5">
              Check out my <Link to="/projects">projects</Link> to see what I've
              been up to! Or check out my <Link to="/blog">blog</Link> to see
              what's recently caught my eye!
            </p>
          </article>
          <hr />
          <article className="w-75 m-auto">
            <p className="unemployed">
              <small>
                I am <b>currently looking for internship opportunities</b>! If
                you like what you <Link to="/resume">see</Link>, let's get
                in&nbsp;
                <a
                  href="mailto:souruly@gmail.com"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  touch
                </a>
                !
              </small>
            </p>
            <hr />
          </article>
        </Container>
      </PageLayout>
    </div>
  )
}
